import searchView from '@/components/search/search';
import goodsItemView from '@/components/goods/item';
export default {
	data() {
		return {
			searchValue: '',
			isShow: 0,
			lists: [],
			start: 0
		}
	},
	components:{searchView,goodsItemView},
	onLoad(e) {
		this.searchValue = e.value;
		this.classid = e.classid;
		this.onReachBottomData();
	},
	onReachBottom(e) {
		this.start += 20;
		this.onReachBottomData();
	},	
	methods: {
		onReachBottomData (e) {
			const t = this;
			uni.request({
				url: t.$serverUrl + '/byclass',
				data: { 
					appkey: t.$appkey,
					classid: t.classid,
					start: t.start,
					num: 20
				},
				success: (ret) => {
					if (ret.statusCode !== 200) {
						t.isShow = !0
						console.log('请求失败', ret)
						return;
					};
					const data = ret.data.result.list;
					t.lists = t.lists.concat(data);
				}
			});
		},
		getData (e) {
			var t = this;
			uni.request({
				url: t.$serverUrl + '/search',
				data: { 
					appkey: t.$appkey,
					keyword: t.searchValue,
					num: 60
				},
				success: (ret) => {
					if (ret.statusCode !== 200) {
						t.isShow = !0
						console.log('请求失败', ret)
						return;
					};
					const data = ret.data.result.list;
					t.lists = data;
				}
			});
			
		},
	}
}
